.message {
    margin-bottom: 55px;
}

.imageAndText {
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    display: block;
    min-height: 60px;
    max-width: 527px;
    position: relative;
    z-index: -1;
    background: #0066CC;
    color: #fff;
    padding: 10px 15px;
    -webkit-border-radius: 10px;
    -webkit-border-bottom-right-radius: 0;
    -moz-border-radius: 10px;
    -moz-border-radius-bottomright: 0;
    border-radius: 10px;
    border-bottom-right-radius: 0;
    margin-right: 15px;
}

.text:after,
.text:before {
    content: "";
    position: absolute;
    left: 100%;
    bottom: 0em;
    width: 1.8em;
    height: 1.8em;
    border: 0.5em solid white;
    border-radius: 50%;
    background: #fff;
}

.text::before {
    border-top: none;
    height: 0.9em;
    border-radius: 0 0 50% 0% / 0 0 100% 100%;
    background: #0066CC;
    border-color: #0066CC;
    margin-left: -0.9em;
}

.name {
    text-align: right;
    font-weight: bold;
    color: #FFFFFF;
    display: flex;
    justify-content: right;
    margin-bottom: 3px;
    font-size: 18px;

}

.messageText {
    display: flex;
    justify-content: right;
    font-size: 16px;
}

.time {
    display: flex;
    justify-content: right;
    padding: 10px 5px 30px 0;
    font-weight: bold;
}

